import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { FichaComercialDatos } from '@app/viewmodel/fichacomercialdatos';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { Candeactivateguard } from '@app/shared/can-deactivate-guard/can-deactivate-guard.service';
import { NotificationService, LocalDataService } from '@app/shared';
import { FichacomercialService } from '@app/services/fichacomercial/fichacomercial.service';
import { FichaComercialPresupuesto } from '@app/viewmodel/fichacomercialpresupuesto';
import { BsLocaleService } from 'ngx-bootstrap/datepicker';
import * as moment from 'moment';
@Component({
  selector: 'app-fichacomercial-presupuesto',
  templateUrl: './fichacomercial-presupuesto.component.html',
  styleUrls: ['./fichacomercial-presupuesto.component.css']
})
export class FichacomercialPresupuestoComponent implements OnInit {

  @Input() item: any;
  @ViewChild('ctrlform') ctrlform: any;
  @ViewChild('gmap') gmapElement: any;
  submit: boolean;
  constructor(private spinnerService: Ng4LoadingSpinnerService,
    private fichacomercialService: FichacomercialService,
    private notificationService: NotificationService,
    private localDataService: LocalDataService,
    private candeactivateguard: Candeactivateguard,
    private localeService: BsLocaleService, ) {

    this.localeService.use('es');
  }

  ngOnInit() {
    this.item.presupuesto = this.item.presupuesto == undefined ? new FichaComercialPresupuesto() : this.item.presupuesto;
    if (this.item.presupuesto.fechaaceptado != undefined) {
      this.item.presupuesto.fechaaceptado = moment(this.item.presupuesto.fechaaceptado).toDate();
    }
    if (this.item.presupuesto.fechainicio != undefined) {
      this.item.presupuesto.fechainicio = moment(this.item.presupuesto.fechainicio).toDate();
    }
    if (this.item.presupuesto.fechafin != undefined) {
      this.item.presupuesto.fechafin = moment(this.item.presupuesto.fechafin).toDate();
    }
    if (this.item.presupuesto.facturacion == undefined) {
      this.item.presupuesto.facturacion = false;
    }
  }
  add() {
    this.submit = true;
    if (this.ctrlform.invalid) {
      return false;
    }
    this.submit = false;
    /*this.item.presupuesto.fechaaceptadof = moment(this.item.presupuesto.fechaaceptado).format("DD-MM-YYYY HH:mm:ss");
    this.item.presupuesto.fechainiciof = moment(this.item.presupuesto.fechainicio).format("DD-MM-YYYY HH:mm:ss");
    this.item.presupuesto.fechafinf = moment(this.item.presupuesto.fechafin).format("DD-MM-YYYY HH:mm:ss");
*/
    this.spinnerService.show();
    this.candeactivateguard.setedit(false);
    this.item.fk_empresa = this.localDataService.getLocalData('empresaSeleccionada').id;
    this.item.paso=2;
    this.fichacomercialService.update(this.item)
      .subscribe((value) => {
        this.spinnerService.hide();
        this.notificationService.onSuccess('Presupuesto actualizado.');
      }, (err) => {
        this.spinnerService.hide();
        if (err.status_code == 422) {
          this.notificationService.onError("Presupuesto  ya existen.");
        }
        else { this.notificationService.onError("Se ha producido un error."); }
      });
  }
  archivopresupuesto(tipoarchivo, $event) {
    //archivos proyectos visado
    if (tipoarchivo == 1) {
      this.item.presupuesto.proyectovisado_arc = this.insertarArchivo($event, this.item.presupuesto.proyectovisado_arc);

    } else if (tipoarchivo == 2) {//Licencia de obra
      this.item.presupuesto.licencia_arc = this.insertarArchivo($event, this.item.presupuesto.licencia_arc);
    } else if (tipoarchivo == 3) {//Plano Implantación
      this.item.presupuesto.plano_arc = this.insertarArchivo($event, this.item.presupuesto.plano_arc);
    } else if (tipoarchivo == 4) {//Plano Instalación Mecanica
      this.item.presupuesto.planoins_arc = this.insertarArchivo($event, this.item.presupuesto.planoins_arc);
    }
  }
  insertarArchivo(archivo, lista) {
    if (lista == undefined) {
      lista = [];
    }
    lista.push(archivo);
    return lista;
  }

}



