import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { defineLocale } from 'ngx-bootstrap/chronos';
import { esLocale } from 'ngx-bootstrap/locale';
import { FichacomercialRoutingModule } from './fichacomercial-routing.module';
import { FichacomercialIndexComponent } from './fichacomercial-index/fichacomercial-index.component';
import { FichacomercialEditComponent } from './fichacomercial-edit/fichacomercial-edit.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { AlertModule } from 'ngx-bootstrap/alert';
import { SweetAlert2Module } from '@toverux/ngx-sweetalert2';
import { SnotifyModule } from 'ng-snotify';
import { HttpModule } from '@angular/http';
import { AuthenticationModule, EnvironmentModule, FormValidationModule, NotificationModule, LocalDataModule } from '@app/shared';
import { TypeaheadModule } from 'ngx-bootstrap/typeahead';

import { FileUploadModule } from 'ng2-file-upload';
import { TagInputModule } from 'ngx-chips';
import { FichacomercialDatosComponent } from './fichacomercial-datos/fichacomercial-datos.component';
import { MapsService } from '@app/google-apis/maps.service';
import { FichacomercialPresupuestoComponent } from './fichacomercial-presupuesto/fichacomercial-presupuesto.component';
import { BsDatepickerModule } from 'ngx-bootstrap/datepicker';
import { ComponentesModule } from '@app/componentes/componentes.module';
import { FichacomercialElementosComponent } from './fichacomercial-elementos/fichacomercial-elementos.component';
defineLocale('es', esLocale);
@NgModule({
  imports: [
    CommonModule,
    FichacomercialRoutingModule,
    FormsModule,
    ReactiveFormsModule,
    // Ng4LoadingSpinnerModule.forRoot(),
    AlertModule.forRoot(),
    SweetAlert2Module.forRoot(),
    SnotifyModule,
    HttpModule,
    AuthenticationModule,
    EnvironmentModule,
    FormValidationModule,
    NotificationModule,
    LocalDataModule,
    TypeaheadModule.forRoot(),
    FileUploadModule,
    TagInputModule,
    BsDatepickerModule.forRoot(),
    ComponentesModule,
  ],
  declarations: [FichacomercialIndexComponent,
    FichacomercialEditComponent,
    FichacomercialDatosComponent,
    FichacomercialPresupuestoComponent, FichacomercialElementosComponent],
  providers:[MapsService]
})
export class FichacomercialModule { }



