import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';


import { Candeactivateguard } from '@app/shared/can-deactivate-guard/can-deactivate-guard.service';

import { Perfil } from '@app/viewmodel/perfil';
import { NotificationService, LocalDataService } from '@app/shared';
import { FichacomercialService } from '@app/services/fichacomercial/fichacomercial.service';
import { FichaComercial } from '@app/viewmodel/fichacomercial';

@Component({
  selector: 'app-fichacomercial-edit',
  templateUrl: './fichacomercial-edit.component.html',
  styleUrls: ['./fichacomercial-edit.component.css']
})
export class FichacomercialEditComponent implements OnInit {
  
  item: FichaComercial;
  submit = false;
  @ViewChild('ctrlform') ctrlform: any;
  usuariosobra: any;
  constructor(
    private spinnerService: Ng4LoadingSpinnerService,
    private fichacomercialService: FichacomercialService,
    private router: Router,
    private route: ActivatedRoute,
    private notificationService: NotificationService,
    private localDataService: LocalDataService,
    private candeactivateguard: Candeactivateguard,
  ) {
    this.candeactivateguard.setedit(true);
    this. getById() ;
  }


  ngOnInit() {
    this.item = new FichaComercial();
   
  }
  getById() {
    this.spinnerService.show();
    const $id = this.route.snapshot.paramMap.get('id');
    this.fichacomercialService.getById($id)
      .subscribe((response) => {
        this.item = response.data.formulario;
        this.usuariosobra = response.data.usuariosobra;
        if(this.item.elementoscampos.length==0){
          this.item.elementoscampos=this.item._elementoscampos;
        }
        this.spinnerService.hide();
      })
  }

  add() {
    this.submit = true;
    if (this.ctrlform.invalid) {
      return false;
    }
    this.submit = false;
    this.spinnerService.show();
    this.candeactivateguard.setedit(false);
    this.item.fk_empresa = this.localDataService.getLocalData('empresaSeleccionada').id;
    this.item.paso=4;
    this.fichacomercialService.update(this.item)
      .subscribe((value) => {
        this.spinnerService.hide();
        this.notificationService.onSuccess('Ficha comercial asignada.');
        this.router.navigateByUrl('obra/fichacomercial');
      }, (err) => {
        this.spinnerService.hide();
        if (err.status_code == 422) {
          this.notificationService.onError("Ficha comercial ya existe");
        }
        else { this.notificationService.onError("Se ha producido un error."); }
      });
  }


}



