import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { FichaComercialDatos } from '@app/viewmodel/fichacomercialdatos';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { Candeactivateguard } from '@app/shared/can-deactivate-guard/can-deactivate-guard.service';
import { NotificationService, LocalDataService } from '@app/shared';
import { FichacomercialService } from '@app/services/fichacomercial/fichacomercial.service';
import { FichaComercial } from '@app/viewmodel/fichacomercial';
import { MapsService } from '@app/google-apis/maps.service';
import { } from '@types/googlemaps';
@Component({
  selector: 'app-fichacomercial-datos',
  templateUrl: './fichacomercial-datos.component.html',
  styleUrls: ['./fichacomercial-datos.component.css']
})
export class FichacomercialDatosComponent implements OnInit {

  @Input() item: any;
  @ViewChild('ctrlform') ctrlform: any;
  @ViewChild('gmap') gmapElement: any;
  map: google.maps.Map;
  markers = [];
  submit: boolean;
  lat = 40.50017;
  long = -3.89239;
  constructor(private spinnerService: Ng4LoadingSpinnerService,
    private fichacomercialService: FichacomercialService,
    private notificationService: NotificationService,
    private localDataService: LocalDataService,
    private candeactivateguard: Candeactivateguard,
    private mapsService: MapsService, ) {
    this.item = new FichaComercialDatos();
  }

  ngOnInit() {
    this.item.datos = this.item.datos == undefined ? new FichaComercialDatos() : this.item.datos;
    this.initmap();
    let myLatlng = new google.maps.LatLng(this.item.estacionservicio.lat, this.item.estacionservicio.lng);
    this.addclickmarker(myLatlng);
  }
  add() {
    this.submit = true;
    if (this.ctrlform.invalid) {
      return false;
    }
    this.submit = false;
    this.spinnerService.show();
    this.candeactivateguard.setedit(false);
    this.item.fk_empresa = this.localDataService.getLocalData('empresaSeleccionada').id;
    this.item.paso=1;
    this.fichacomercialService.update(this.item)
      .subscribe((value) => {
        this.spinnerService.hide();
        this.notificationService.onSuccess('Los datos basicos actualizados.');
      }, (err) => {
        this.spinnerService.hide();
        if (err.status_code == 422) {
          this.notificationService.onError("Los datos basicos  ya existen.");
        }
        else { this.notificationService.onError("Se ha producido un error."); }
      });
  }
  addclickmarker(myLatlng) {
    let marker = new google.maps.Marker({
      position: myLatlng,
      map: this.map,
      animation: google.maps.Animation.DROP,
      title: 'Hola!',
      label: 'D'
    });
    this.setMapOnAll(null);
    this.markers.push(marker);
    this.map.setCenter(marker.getPosition());
  }
  setMapOnAll(map) {
    for (let i = 0; i < this.markers.length; i++) {
      this.markers[i].setMap(map);
    }
    this.markers = [];
  }
  initmap() {

    let myLatlng = new google.maps.LatLng(this.lat, this.long);
    let mapProp = {
      center: myLatlng,
      zoom: 15,
      mapTypeId: google.maps.MapTypeId.ROADMAP,

    };
    this.markers = [];
    this.map = new google.maps.Map(this.gmapElement.nativeElement, mapProp);
    google.maps.event.addListener(this.map, 'click', (event) => {
      let myLatLng = event.latLng;
      let lat = myLatLng.lat();
      let lng = myLatLng.lng();
      this.item.lat = lat;
      this.item.lng = lng;
      this.addclickmarker(myLatLng);


    });
  }

}



