import { Component, OnInit, Input, ChangeDetectorRef, AfterContentChecked } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { FichacomercialService } from '@app/services/fichacomercial/fichacomercial.service';
import { NotificationService, LocalDataService } from '@app/shared';
import { Candeactivateguard } from '@app/shared/can-deactivate-guard/can-deactivate-guard.service';
import { BsLocaleService } from 'ngx-bootstrap/datepicker';

@Component({
  selector: 'app-fichacomercial-elementos',
  templateUrl: './fichacomercial-elementos.component.html',
  styleUrls: ['./fichacomercial-elementos.component.css']
})
export class FichacomercialElementosComponent implements OnInit, AfterContentChecked {
  @Input() item: any;
  ctrlform: FormGroup;
  submit: boolean;
  constructor(private spinnerService: Ng4LoadingSpinnerService,
    private fichacomercialService: FichacomercialService,
    private notificationService: NotificationService,
    private localDataService: LocalDataService,
    private candeactivateguard: Candeactivateguard,
    private localeService: BsLocaleService,
    private cdRef: ChangeDetectorRef) { }

  ngOnInit() {
    this.submit = false;
    let group = {};
    this.item.elementoscampos.forEach(input_template => {
      if (input_template.tipo != 'Split') {
        group['ctrl_' + input_template.elementocampo.id] = this.createcontrol(input_template.elementocampo);
      }
    });
    this.ctrlform = new FormGroup(group);
  }
  get f() {
    return this.ctrlform.controls;
  }
  createcontrol(element) {

    if (element.requerido == false) {
      return new FormControl('', []);
    } else if (element.tipo == 'Checkbox') {
      return new FormControl('', [Validators.requiredTrue]);
    } else if (element.tipo == 'Lista') {
      return new FormControl('', [Validators.required]);
    } else if (element.tipo == 'Numerico') {
      return new FormControl('', [Validators.required]);
    }
  }
  add() {
    this.submit = true;
    if (this.ctrlform.invalid) {
      return false;
    }
    this.submit = false;
    this.spinnerService.show();
    this.candeactivateguard.setedit(false);
    this.item.fk_empresa = this.localDataService.getLocalData('empresaSeleccionada').id;
    this.item.paso=3;
    this.fichacomercialService.update(this.item)
      .subscribe((value) => {
        this.spinnerService.hide();
        this.notificationService.onSuccess('Presupuesto actualizado.');
      }, (err) => {
        this.spinnerService.hide();
        if (err.status_code == 422) {
          this.notificationService.onError("Presupuesto  ya existen.");
        }
        else { this.notificationService.onError("Se ha producido un error."); }
      });
  }
  ngAfterContentChecked(): void {
    this.cdRef.detectChanges();
  }

  archivoelemento(elemento, event) {

    if (elemento.archivos == undefined) {
      elemento.archivos = [];
    }
    elemento.archivos = this.insertarArchivo(event, elemento.archivos);
  }
  insertarArchivo(archivo, lista) {
    if (lista == undefined) {
      lista = [];
    }
    lista.push(archivo);
    return lista;
  }

}



