import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { FichacomercialIndexComponent } from './fichacomercial-index/fichacomercial-index.component';
import { Candeactivateguard } from '@app/shared/can-deactivate-guard/can-deactivate-guard.service';
import { FichacomercialEditComponent } from './fichacomercial-edit/fichacomercial-edit.component';

const routes: Routes = [ { path: '', component: FichacomercialIndexComponent},
{ path: 'edit/:id', component: FichacomercialEditComponent, canDeactivate: [Candeactivateguard]}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class FichacomercialRoutingModule { }



